.cellTotal {
    float: left;
    margin: 2px 0 0 2px;
    box-sizing: border-box;
    position: relative;
}
.lvlBack {
    width: 100%;
    height: 100%;

    text-align: center;
    color: #fff;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.lvlFront {
    width: 0;
    height: 0;

    opacity: 0.5;
    position: absolute;
    top: 0;
    display: none;
}

.cellTotal:hover .lvlFront {
    display: block;
}
.status {
    font-size: 18pt;
    text-align: center;
}


.field {
    margin: 40px 0;
    padding: 2px;
    background-color: #aaa;
    border: 4px solid #558;
}
.field .cellTotal:nth-of-type(1),
.field .cellTotal:nth-of-type(2),
.field .cellTotal:nth-of-type(3),
.field .cellTotal:nth-of-type(4) {
    margin-top: 0;
}
.field .cellTotal:nth-of-type(4n-3) {
    margin-left: 0;
}


.btnRestart {
    display: block !important;
    margin: 0 auto !important;
    width: 162px !important;
    font-size: 10pt !important;

    background-color: #aae !important;
}
.btnRestart:hover {
    background-color: #aea !important;
}
.btnRestart:disabled {
    color: #333 !important;
    background-color: #ccc !important;
}
body {
	margin: 0;
}
#root {
	background-color: #eee;
	min-height: 100vh;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
